import React, { useState } from 'react'
import classes from './Block.module.css'
import rat from '../../grid_images/rat.png'
import cheese from '../../grid_images/cheese.png'
import green from '../../grid_images/green.png'

const Block = (props) => {
    console.log("child")
    const list = {
        rat: rat,
        cheese: cheese,
        green: green
    }

    const [blocker, setBlocked] = useState('0');

const blockerHandler = (event) => {
    event.preventDefault();
    if(props.id === 11 || props.id === 55) return ;

    let temp = '0';
    if(blocker === '0' ){
        temp = '1';
        setBlocked('1');
    }
    else{
        temp = '0';
        setBlocked('0');
    }
    props.blocker(props.id, temp);
}

    return(
        <div className = {`${classes.shape} ${+blocker && classes.blocker}`} id = {props.id} onClick = {blockerHandler}>
            {
                (props.image !== "e") && 
                (<img style = {{ maxWidth: '100%', maxHeight: '100%'}} 
                src = {list[props.image]} alt = ""></img>)
            }
        </div>
    )
};

export default Block;